.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		svg {
			rect {
				stroke: var(--color-light);
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 280px;
		transform: translate(-50%, -20%);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 200px;
			transform: translate(-60%, -20%);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 80% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 280px;
		transform: translate(50%, -20%) scaleX(-1);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 200px;
			transform: translate(60%, -20%) scaleX(-1);
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 80% 0%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 5%;
		width: 260px;
		transform: translate(0, -25%);
		@include max-lg {
			left: 0;
			transform: translate(-20%, -25%);
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translateX(50%);
			@include max-md {
				width: 180px;
				transform: translateX(40%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 5%;
		width: 260px;
		transform: translate(0, -25%) scaleX(-1);
		@include max-lg {
			right: 0;
			transform: translate(20%, -25%) scaleX(-1);
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translateX(50%);
			@include max-md {
				width: 180px;
				transform: translateX(40%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 25%;
		width: 120px;
		transform: translate(-50%, 20%);
		@include max-lg {
			left: 10%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 200px;
			transform: translate(-50%, 0%);
			img {
				transform-origin: 50% 70%;
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 25%;
		width: 120px;
		transform: translate(50%, 20%) scaleX(-1);
		@include max-lg {
			right: 10%;
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 50%;
			width: 200px;
			transform: translate(50%, 0%) scaleX(-1);
			img {
				transform-origin: 50% 70%;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				margin-top: -5rem;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.foreground-element {
					width: 100%;
					height: 100%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: rgba($color: #fff, $alpha: 0.5);
						filter: blur(5px);
						content: "";
					}
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
