.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: transparent;
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 45%;
		transform: translate(-20%, -20%);
		@include max-lg {
			width: 80%;
		}
		@include max-md {
			width: 125%;
		}
		@include max-xs {
			width: 110%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 90%;
			transform: translateX(40%);
			@include max-lg {
				transform: translateX(10%);
			}
			@include max-md {
				transform: translateX(0);
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 45%;
		transform: translate(20%, -20%) scaleX(-1);
		@include max-lg {
			width: 80%;
		}
		@include max-md {
			width: 125%;
		}
		@include max-xs {
			width: 110%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 90%;
			transform: translateX(40%);
			@include max-lg {
				transform: translateX(10%);
			}
			@include max-md {
				transform: translateX(0);
			}
		}
	}
	&.cover-opening {
		.orn-1,
		.orn-3 {
			.image-element {
				transition: transform 2.5s ease, opacity 2.5s ease;
				transition-delay: 200ms;
				transform: translateX(-100%);
				opacity: 0;
				@include max-lg {
					transition-delay: 700ms;
				}
			}
			.orn-2,
			.orn-4 {
				.image-element {
					transition: transform 2.7s ease, opacity 2.7s ease;
					transform: translateX(-120%);
					opacity: 0;
				}
			}
		}
		.cover-content {
			opacity: 0;
		}
	}
	&.cover-opened {
		display: none;
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-light);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: opacity 2500ms ease;
			.cover-body {
				position: relative;
				width: 40%;
				margin: 0 auto;
				@include max-lg {
					width: 60%;
				}
				@include max-md {
					width: 100%;
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 3/4;
					&::before {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba($color: #000000, $alpha: 0.3);
						content: "";
						border-radius: 500px 500px 20px 20px;
					}
					&::after {
						position: absolute;
						top: 10px;
						right: 10px;
						bottom: 10px;
						left: 10px;
						background-image: url(../images/border-gold.png);
						background-size: 100% 100%;
						background-repeat: no-repeat;
						pointer-events: none;
						content: "";
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 500px 500px 20px 20px;
					}
				}
				.cover-description {
					position: absolute;
					bottom: 2rem;
					left: 0;
					width: 100%;
					padding: 0 1rem;
					text-align: center;
					.greeting-wrapper {
						position: relative;
						@include max-md {
							font-size: 14px;
						}
						h6 {
							font-weight: 700;
						}
					}
					h6 {
						font-weight: 600;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
