.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 280px;
		transform: translate(-50%, -20%);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 180px;
			transform: translate(-60%, -20%);
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 80% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 280px;
		transform: translate(50%, -20%) scaleX(-1);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 180px;
			transform: translate(60%, -20%) scaleX(-1);
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 80% 0%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 5%;
		width: 260px;
		transform: translate(0, -25%);
		@include max-lg {
			left: 0;
			transform: translate(-20%, -25%);
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translateX(50%);
			@include max-md {
				width: 180px;
				transform: translateX(40%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 5%;
		width: 260px;
		transform: translate(0, -25%) scaleX(-1);
		@include max-lg {
			right: 0;
			transform: translate(20%, -25%) scaleX(-1);
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translateX(50%);
			@include max-md {
				width: 180px;
				transform: translateX(40%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 25%;
		width: 120px;
		transform: translate(-50%, 20%);
		@include max-lg {
			left: 20%;
		}
		@include max-md {
			left: 0;
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 200px;
			transform: translate(-50%, 0%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
			img {
				transform-origin: 50% 70%;
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 25%;
		width: 120px;
		transform: translate(50%, 20%) scaleX(-1);
		@include max-lg {
			right: 20%;
		}
		@include max-md {
			right: 0;
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 50%;
			width: 200px;
			transform: translate(50%, 0%) scaleX(-1);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
			img {
				transform-origin: 50% 70%;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 5rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
