.rsvp-section {
	position: relative;
	padding: 5rem 0;
	background: rgba($color: #000000, $alpha: 0.2);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-40%, 0);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
			transform: translate(-50%, 0);
		}
		@include max-xs {
			width: 140px;
		}
		.orn-2 {
			position: relative;
			width: 140px;
			transform: translate(50%, -25%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-3 {
				position: relative;
				width: 140px;
				transform: translate(-20%, -55%);
				@include max-lg {
					width: 120px;
				}
				@include max-md {
					width: 100px;
				}
				@include max-xs {
					width: 80px;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 140px;
		transform: translate(30%, 0);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
			transform: translate(40%, 0);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-5 {
			position: relative;
			width: 200px;
			transform: translate(0%, -180%) rotate(-13deg);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 140px;
		transform: translate(-50%, -42%);
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 2rem 3rem;
		background: rgba($color: #fff, $alpha: 0.5);
		border-radius: 500px 500px 20px 20px;
		color: var(--color-dark);
		overflow: hidden;
		@include max-md {
			padding: 8rem 1rem 2rem;
		}
		.rsvp-form {
			position: relative;
		}
	}
}
