.story-section {
	position: relative;
	background: rgba($color: #000000, $alpha: 0.2);
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 200px;
		transform: translate(-60%, 0);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 0;
			width: 160px;
			transform: translate(-70%, 0);
		}
		@include max-xs {
			width: 140px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 20rem;
		right: 0;
		width: 200px;
		transform: translate(60%, 0);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			bottom: 38rem;
			width: 160px;
		}
		@include max-xs {
			bottom: 30rem;
			width: 140px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 40%;
		left: 0;
		width: 240px;
		transform: translate(-50%, -40%) rotate(-12deg);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
	}
	.orn-4 {
		position: absolute;
		top: 40%;
		right: 0;
		width: 240px;
		transform: translate(30%, -40%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
	}
	.orn-5 {
		position: relative;
		width: 160px;
		margin-top: -6rem;
		@include max-lg {
			width: 130px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(50%, 0);
			@include max-lg {
				width: 120px;
				transform: translate(40%, 0);
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(0, 0) scaleX(-1);
		@include max-lg {
			width: 130px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 50% 0%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(50%, 0);
			@include max-lg {
				width: 120px;
				transform: translate(40%, 0);
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 7/8;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 500px 500px 20px 20px;
				z-index: 5;
			}
		}
	}
}
