.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.reminder-content {
		position: relative;
		max-width: 100%;
		padding: 3rem;
		z-index: 2;
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		border-radius: var(--border-radius);
		overflow: hidden;
		@include max-md {
			padding: 3rem 1rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
