.couple-section {
	position: relative;
	text-align: center;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 140px;
		transform: translate(-20%, 0);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			top: 0;
			width: 100px;
			transform: translate(-30%, 0);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-2 {
			position: relative;
			width: 140px;
			transform: translateY(-30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-3 {
				position: relative;
				width: 180px;
				transform: translate(-40%, -50%);
				@include max-lg {
					width: 160px;
				}
				@include max-md {
					width: 140px;
				}
				@include max-xs {
					width: 120px;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 140px;
		transform: translate(20%, 0);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			top: 0;
			width: 100px;
			transform: translate(30%, 0);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-5 {
			position: relative;
			width: 140px;
			transform: translateY(-30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-6 {
				position: relative;
				width: 200px;
				transform: translate(10%, -50%) scaleX(-1);
				@include max-lg {
					width: 180px;
				}
				@include max-md {
					width: 160px;
				}
				@include max-xs {
					width: 140px;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		top: 70%;
		left: 0;
		width: 140px;
		transform: translate(-20%, -20%);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			top: 50%;
			width: 100px;
			transform: translate(-30%, -20%);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-8 {
			position: relative;
			width: 140px;
			transform: translate(0, -40%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-9 {
				position: relative;
				width: 200px;
				transform: translate(-20%, -60%) scaleX(-1);
				@include max-lg {
					width: 180px;
				}
				@include max-md {
					width: 140px;
				}
				@include max-xs {
					width: 120px;
				}
			}
		}
	}
	.orn-10 {
		position: absolute;
		top: 70%;
		right: 0;
		width: 180px;
		transform: translate(50%, -20%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		.orn-11 {
			position: relative;
			width: 120px;
			transform: translate(30%, -20%);
			@include max-lg {
				width: 100px;
			}
			@include max-md {
				width: 80px;
			}
			.orn-12 {
				position: relative;
				width: 120px;
				transform: translate(10%, -40%);
				@include max-lg {
					width: 100px;
				}
				@include max-md {
					width: 80px;
				}
			}
		}
	}
	.orn-13 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-20%, 0) rotate(10deg);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(20%, -30%);
		@include max-lg {
			width: 140px;
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		padding: 8rem 3rem 3rem;
		border-radius: 500px 500px 20px 20px;
		background-color: rgba($color: #000000, $alpha: 0.2);
		margin-bottom: 3rem;
		color: var(--color-light);
		@include max-md {
			padding: 8rem 1rem 2rem;
		}
		&::before {
			position: absolute;
			top: 0;
			left: 50%;
			width: 140px;
			height: 85px;
			background-image: url(../images/pattern-4.png);
			background-size: 100%;
			background-repeat: no-repeat;
			transform: translate(-50%, -42%);
			pointer-events: none;
			content: "";
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 10px;
				background-image: url(../images/border-gold.png);
				background-size: 100% 100%;
				background-repeat: no-repeat;
				&::before {
					position: absolute;
					top: 0;
					left: 50%;
					width: 100px;
					height: 100px;
					transform: translate(-50%, -42%);
					background-image: url(../images/ornament-gold.png);
					background-size: 100%;
					background-repeat: no-repeat;
					z-index: 2;
					content: "";
					@include max-md {
						width: 80px;
						height: 80px;
					}
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px 500px 20px 20px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 3rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
