.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-25%, 0);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
			transform: translate(-35%, 0);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-2 {
			position: relative;
			width: 140px;
			transform: translateY(-30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-3 {
				position: relative;
				width: 220px;
				transform: translate(-50%, -50%);
				@include max-lg {
					width: 180px;
				}
				@include max-md {
					width: 140px;
				}
				@include max-xs {
					width: 120px;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(30%, 0);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
			transform: translate(40%, 0);
		}
		@include max-xs {
			width: 80px;
		}
		.orn-5 {
			position: relative;
			width: 140px;
			transform: translateY(-30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			.orn-6 {
				position: relative;
				width: 200px;
				transform: translate(10%, -50%) rotate(10deg) scaleX(-1);
				@include max-lg {
					width: 180px;
				}
				@include max-md {
					width: 140px;
				}
				@include max-xs {
					width: 120px;
				}
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 8rem 2rem 3rem;
		border-radius: 500px 500px 20px 20px;
		overflow: hidden;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
	}
}
